body {
  font-family: 'Roboto Condensed', sans-serif;
  font-weight: 300;
  font-size: 1.2rem;
  line-height: 1.5;
  color: black; }
  body h2,
  body button,
  body footer {
    font-weight: 400;
    color: white;
    background-color: #1a71a9; }

#wrapper {
  background-image: url(../images/bg.jpg);
  background-repeat: repeat; }

header .toggle {
  font-size: 5rem; }

#side-bar h2 {
  text-transform: uppercase; }

.recipe-item {
  display: flex;
  flex-direction: row;
  text-transform: uppercase;
  margin: 0.4rem;
  align-items: center;
  padding: 0.6rem 0; }

.recipe-item p {
  margin-left: 1rem 0.5rem; }

.recipe-item img {
  margin: 0.1rem 0.6rem;
  /* border: 1px solid  */
  box-shadow: -3px -3px, 0 -3px, 1px -3px, -3px 1px, 1px 1px; }

.recipe-item.white {
  background-color: white; }

.recipe-item.grey {
  background-color: #E9E9E9; }

/* cuisine */
.cuisine-wrapper ul li {
  list-style: none;
  margin: 0.9rem 0;
  width: 50%; }

.cuisine-list-wrapper {
  columns: 2; }

/* media query */
/* tablet small */
/* variables add */
@media (min-width: 500px) {
  .cuisine-list-wrapper {
    columns: 3; } }

.footer {
  text-align: center; }
